import { Component, Element, Env, Event, EventEmitter, h, Host, Method, Prop, State, Watch } from '@stencil/core';
import { FnIcon, FnTag } from '@utils/functional/functional';
import { createLogger, debounce, deepEqual, isNonEmptyValue, mergeClasses } from '@utils/utils';
import flatpickr from 'flatpickr';
import { MandarinTraditional } from 'flatpickr/dist/l10n/zh-tw.js';
import monthSelectPlugin from 'flatpickr/dist/plugins/monthSelect';
import { DateInputMode, InputType } from './interface';

@Component({
  tag: 'storm-input-group',
  styleUrl: 'storm-input-group.css',
  shadow: false,
})
export class StormInputGroup {
  private readonly logger = createLogger('storm-input-group', Env?.dev);

  private __textInputRef: HTMLInputElement;
  private __cancelRef: HTMLDivElement;
  private __groupRef: HTMLDivElement;
  private __flatpickrInstance: any;
  private __valueChangedEvent?: string | string[];

  @Element() host: HTMLElement;

  @Prop({ mutable: true }) value: string | string[];
  @Prop() type: InputType = InputType.Text;
  @Prop() label: string;

  // Date picker
  @Prop() enableTime: boolean = false;
  @Prop() noCalendar: boolean = false;
  @Prop() dateFormat: string = 'Y-m-d';
  @Prop() mode: DateInputMode = DateInputMode.Single;

  @Prop() name: string;
  @Prop() disabled: boolean = false;
  @Prop() clearable: boolean = true;
  @Prop() inputStyle: 'outline' | 'dynamic' | 'static' = 'static';
  @Prop() size?: 'lg' | 'sm';
  @Prop() placeholder: string;
  @Prop() appendIcon: string;
  @Prop() prependIcon: string;
  @Prop() class: Record<string, boolean> | string;
  @Prop() multiple?: boolean;

  @Prop({ mutable: true }) valid?: boolean;
  @Prop() required?: boolean;
  @Prop() isDynamic?: boolean;
  @Prop() outline?: boolean;

  @State() private isValid?: boolean;

  @Event() valueChanged: EventEmitter<string | string[]>;
  @Event() valueChangedOnBlur: EventEmitter<string | string[]>;

  @Watch('value')
  valueWatcher(newValue: string | string[], oldValue: string | string[]) {
    this.logger('valueWatcher', { newValue, oldValue, hasFocus: !!this.host.querySelector(':focus') });
    if (deepEqual(newValue, oldValue)) return;
    if (this.host.querySelector(':focus')) {
      this.__valueChangedEvent = newValue;
    } else if (this.__valueChangedEvent != null) {
      this.__valueChangedEvent = newValue;
      this.emitValueChangeOnBlur();
    }
    this.updateValueOnChange(newValue);
  }

  @Method()
  async setFocus() {
    this.logger('setFocus', 'Method called');
    this.debouncedFocusTextInput();
  }

  @Method()
  async setValue(vlaue: string | string[]) {
    this.logger('setValue', { newValue: vlaue, currentValue: this.value });
    this.debouncedUpdateValue(vlaue);
  }

  @Method()
  async flush() {
    if (!this.multiple && this.__textInputRef) {
      const currentValue = this.__textInputRef.value;
      if (!deepEqual(this.value, currentValue)) {
        this.value = currentValue;
      }
    }

    if (this.__valueChangedEvent !== undefined) {
      this.valueChangedOnBlur.emit(this.__valueChangedEvent);
      this.__valueChangedEvent = undefined;
    }
  }

  validate(): void {
    if (this.valid !== undefined || !this.required) return;
    this.isValid = isNonEmptyValue(this.value);
    this.logger('validate', { isValid: this.isValid, value: this.value, required: this.required });
  }

  handleFocus(_: FocusEvent) {
    this.logger('handleFocus', { value: this.value });
    this.__groupRef.classList.add('is-focused');
  }

  handleBlur(_: FocusEvent) {
    // 
    if (!this.multiple && this.__textInputRef) {
      const currentValue = this.__textInputRef.value;
      this.logger('handleBlur', {
        currentValue,
        propValue: this.value,
        isEqual: deepEqual(this.value, currentValue),
        multiple: this.multiple,
      });
      if (deepEqual(this.value, currentValue)) return;
      this.updateValueOnChange(currentValue);
    }

    this.emitValueChangeOnBlur();
    if (!this.__groupRef || !this.__textInputRef) return;

    this.updateInputStatus(this.__textInputRef.value);
    this.__groupRef.classList.remove('is-focused');
  }

  private updateValueOnChange(value: string | string[]) {
    this.value = value;
    this.updateFlatpickr();
    this.valueChanged.emit(value);
    this.validate();
  }

  handleTagCancel(ev: MouseEvent, value: string) {
    if (!Array.isArray(this.value)) return;
    ev.stopPropagation();
    const newValue = this.value.filter(x => x.toString() !== value);
    this.logger('handleTagCancel', { removedValue: value, oldArray: this.value, newArray: newValue });
    this.debouncedUpdateValue(newValue);
  }

  handleKeyDown(ev: KeyboardEvent): void {
    const inputEl = this.__textInputRef;
    if (!inputEl) return;

    const key = ev.key;
    if (key !== 'Enter' && key !== 'Backspace') return;

    const rawInput = inputEl.value ?? '';

    if (!this.multiple) {
      if (deepEqual(this.value, rawInput)) return;
      if (key === 'Enter') {
        this.updateValueOnChange(rawInput);
      }
      return;
    }

    const trimmed = rawInput.trim();
    const currentValues = Array.isArray(this.value) ? this.value : [];

    if (key === 'Enter') {
      ev.preventDefault();
      const exists = currentValues.includes(trimmed);

      this.logger('handleKeyDown', {
        key: 'Enter',
        action: exists ? 'duplicate' : 'add',
        inputValue: trimmed,
        currentArray: currentValues,
      });

      if (!exists) {
        this.debouncedUpdateValue([...currentValues, trimmed]);
      }

      inputEl.value = '';
      this.updateInputStatus('');
    } else if (key === 'Backspace') {
      if (rawInput === '' && currentValues.length > 0) {
        const removed = currentValues[currentValues.length - 1];

        this.logger('handleKeyDown', {
          key: 'Backspace',
          action: 'remove-last',
          currentArray: currentValues,
          removedValue: removed,
        });

        this.debouncedUpdateValue(currentValues.slice(0, -1));
      }
    }
  }

  handleInput(_: InputEvent) {
    this.logger('handleInput', { inputValue: this.__textInputRef.value, multiple: this.multiple });
    if (this.multiple) {
      this.updateInputStatus(this.__textInputRef.value);
    } else {
      this.debouncedUpdateValue(this.__textInputRef.value);
    }
  }

  updateInputStatus(value?: string) {
    const hasValue = value != '' && value != null;
    this.logger('updateInputStatus', { value, hasValue, inputStyle: this.inputStyle });
    if (this.__cancelRef) {
      this.__cancelRef.classList.toggle('d-none', !hasValue);
    }
    if (this.__groupRef) {
      this.__groupRef.classList.toggle('is-filled', this.inputStyle !== 'static' && hasValue);
    }
  }

  clearValue() {
    this.logger('clearValue', { oldValue: this.value, multiple: this.multiple });
    this.__textInputRef.value = '';
    if (!this.multiple) {
      this.debouncedUpdateValue('');
    }
    this.updateInputStatus(this.__textInputRef.value);
  }

  getClasses(): Record<string, boolean> {
    return mergeClasses(this.class, {
      'input-group': true,
      'input-group-lg': this.size === 'lg',
      'input-group-sm': this.size === 'sm',
      'input-group-outline': this.inputStyle === 'outline',
      'input-group-dynamic': this.inputStyle === 'dynamic',
      'input-group-static': this.inputStyle === 'static',
      'is-valid': this.valid === true || this.isValid === true,
      'is-invalid': this.valid === false || this.isValid === false,
      'is-filled': this.value != '' && ((this.disabled === true && this.inputStyle === 'dynamic') || (!this.disabled && this.inputStyle !== 'static')),
    });
  }

  getInputClasses(): Record<string, boolean> {
    const classes = { 'form-control': true, 'multisteps-form__input': true };
    if (this.type == InputType.Date) classes['datetimepicker'] = true;
    return classes;
  }

  componentWillLoad() {
    this.logger('componentWillLoad', { isDynamic: this.isDynamic, outline: this.outline, inputStyle: this.inputStyle, value: this.value });
    if (this.isDynamic != undefined || this.outline != undefined) {
      if (this.outline === true) {
        this.inputStyle = 'outline';
      } else if (this.isDynamic === true) {
        this.inputStyle = 'dynamic';
      } else {
        this.inputStyle = 'static';
      }
    }
  }

  componentWillRender() {
    this.validate();
  }

  appendRender() {
    if (!this.appendIcon) return;
    return (
      <span class="input-group-text">
        <FnIcon>{this.appendIcon}</FnIcon>
      </span>
    );
  }

  prependRender() {
    if (!this.prependIcon) return;
    return (
      <span class="input-group-prepend">
        <FnIcon>{this.prependIcon}</FnIcon>
      </span>
    );
  }

  renderClearButton() {
    if (this.disabled || this.clearable === false) return;
    return (
      <div
        ref={elm => (this.__cancelRef = elm)}
        role="button"
        tabindex="0"
        aria-label="Clear input"
        class={{
          'input-group-clearable': true,
          'p-2': true,
          'd-none': true,
        }}
        onClick={ev => {
          ev.stopPropagation();
          this.clearValue();
          this.debouncedFocusTextInput();
        }}
      >
        <FnIcon>cancel</FnIcon>
      </div>
    );
  }

  labelRender() {
    if (!this.label) return;
    return <label class={{ 'form-label': this.inputStyle != 'static' }}>{this.label}</label>;
  }

  tagRender() {
    if (!Array.isArray(this.value)) return;
    return this.value.map(x => <FnTag lable={x} onClickCancel={ev => this.handleTagCancel(ev, x)}></FnTag>);
  }

  render() {
    return (
      <Host>
        <div ref={elm => (this.__groupRef = elm)} class={this.getClasses()}>
          {this.labelRender()}
          <input
            ref={elm => (this.__textInputRef = elm)}
            class={this.getInputClasses()}
            type={this.type != InputType.Number ? 'text' : InputType.Number}
            value={Array.isArray(this.value) ? '' : this.value}
            name={this.name}
            disabled={this.disabled}
            onFocus={ev => this.handleFocus(ev)}
            onBlur={ev => this.handleBlur(ev)}
            onKeyDown={ev => this.handleKeyDown(ev)}
            onInput={ev => this.handleInput(ev)}
            placeholder={this.placeholder}
          />
          {this.prependRender()}
          {this.appendRender()}
          {this.renderClearButton()}
        </div>
        {this.tagRender()}
      </Host>
    );
  }

  updateFlatpickr() {
    if (this.__flatpickrInstance && this.value && this.type === InputType.Date) {
      const parsedDate = new Date(this.value as string);
      if (!isNaN(parsedDate.getTime())) {
        this.logger('updateFlatpickr', { value: this.value, parsedDate });
        this.__flatpickrInstance.setDate(parsedDate, true);
      } else {
        this.logger('updateFlatpickr', { error: 'Invalid date', value: this.value });
      }
    }
  }

  componentDidLoad() {
    this.logger('componentDidLoad', { type: this.type, value: this.value });
    if (this.type == InputType.Date) {
      const { defaultHour, defaultMinute } = this.getDefaultTime();
      const options = {
        allowInput: false,
        enableTime: this.enableTime,
        noCalendar: this.noCalendar,
        dateFormat: this.dateFormat,
        mode: this.mode,
        time_24hr: true,
        locale: MandarinTraditional,
        plugins: [],
        defaultHour: defaultHour,
        defaultMinute: defaultMinute,
      };

      if (this.dateFormat.lastIndexOf('d') == -1 && this.dateFormat.lastIndexOf('H') == -1 && this.dateFormat.lastIndexOf('h') == -1) {
        options.plugins = [
          monthSelectPlugin({
            // shorthand: false, //defaults to false
            dateFormat: this.dateFormat, //defaults to "F Y"
            // altFormat: "Y-M", //defaults to "F Y"
            // theme: "light" // defaults to "light"
          }),
        ];
      }
      this.logger('componentDidLoad', { action: 'flatpickr-initialized', options });
      this.__flatpickrInstance = flatpickr(this.__textInputRef, options);
    }
  }

  componentDidRender() {
    this.updateInputStatus(this.__textInputRef.value);
  }

  private getDefaultTime(): { defaultHour: number; defaultMinute: number } {
    const now = new Date();
    let defaultHour = now.getHours();
    let defaultMinute = now.getMinutes();

    if (this.value && this.enableTime) {
      const parsedDate = new Date(this.value as string);
      if (!isNaN(parsedDate.getTime())) {
        defaultHour = parsedDate.getHours();
        defaultMinute = parsedDate.getMinutes();
      }
    }

    return { defaultHour, defaultMinute };
  }

  private readonly debouncedFocusTextInput = debounce(() => {
    this.__textInputRef.focus();
  }, 50);

  private readonly debouncedUpdateValue = debounce((value: string | string[]) => {
    this.logger('debouncedUpdateValue', { newValue: value, oldValue: this.value, isEqual: deepEqual(this.value, value) });
    if (deepEqual(this.value, value)) return;
    this.value = value;
  }, 500);

  private readonly emitValueChangeOnBlur = debounce(() => {
    if (this.__valueChangedEvent !== undefined) {
      this.logger('emitValueChangeOnBlur', { value: this.__valueChangedEvent });
      this.valueChangedOnBlur.emit(this.__valueChangedEvent);
      this.__valueChangedEvent = undefined;
    }
  }, 250);
}
